const BlogCard = ({cardInfo})=> {
    return (

        <div className="col-sm-6 col-md-4 col-lg-3 p-2">
                    <div className="card p-3">
                        <div className="img_wrapper">
                            <a href="#">
                            <img className="card-img-top"
                            src={cardInfo.src} alt="" />


                            </a>
                        </div>
                        <div className="card-body m-0 p-0 pt-2">
                            <a href="#" className="person_info py-3 d-block w-100">
                                <img className="w_20 rounded-circle pr-2" src={cardInfo.person_src} alt=""/>
                                <span>
                                    {cardInfo.name}
                                </span>
                            </a>
                            <div className="date">
                                <i className="fas pr-1 text-black-50 fa-clock    "></i>
                                {cardInfo.date}
                            </div>
                            <h5><a href="#" className="d-block">
                                {cardInfo.anchor}
                                </a></h5>

                                <p>
                                    {cardInfo.more_info}
                                </p>
                        </div>
                    </div>
                </div>
    )
}