import React, { useEffect } from 'react';
import Swiper from 'swiper';
import './section4.scss';

export default function Section4() {
    return (
        <section className="section_4 py-5 container-fluid">
            <div className="row">
                <div className="col-sm-5">
                    <img src="https://dreamguys.co.in/demo/doccure/assets/img/features/feature.png"
                        className="d-block w-100" alt="" />

                </div>

                <div className="col-sm-7">
                    <h2>
                        Availabe Features in Our Clinic
                    </h2>
                    <h6 className="text-black-50">
                        It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout.
                    </h6>

                    <SwiperSlide />

                </div>
            </div>
          
        </section>
    )
}

const SwiperSlide = () => {
    useEffect(() => {
        var swiper2 = new Swiper('.swiper3', {
            slidesPerGroup: 1,
            loop: true,
            loopFillGroupWithBlank: true,
            breakpoints: {
                640: {
                  slidesPerView: 2,
                  spaceBetween: 40,
                },
                
                850: {
                    slidesPerView: 3,
                  spaceBetween: 50, 
                }
              },

            pagination: {
                el: '.swiper-pagination3',
                clickable: true,
            },
            navigation: {
                nextEl: '.swiper-button-next3',
                prevEl: '.swiper-button-prev3',
            },
        });
    }, []);


    return (

        <div className="swiper-container swiper3">
            <div className="swiper-wrapper ">
        
            <div className="swiper-slide">
                    <div>
                        <div className="item d-flex justify-content-center align-items-center mb-4">
                            <img className="d-block w-100 rounded-circle"  src="https://dreamguys.co.in/demo/doccure/assets/img/features/feature-06.jpg" alt="" />
                        </div>
                        <h5>
                        Medical
                            
              </h5>

                    </div>
                </div>
             
                <div className="swiper-slide">
                    <div>
                        <div className="item d-flex justify-content-center align-items-center mb-4">
                            <img className="d-block w-100 rounded-circle"  src="https://dreamguys.co.in/demo/doccure/assets/img/features/feature-01.jpg" alt="" />
                        </div>
                        <h5>
                        Patient Word
                            
              </h5>

                    </div>
                </div>

                <div className="swiper-slide">
                    <div>
                        <div className="item d-flex justify-content-center align-items-center mb-4">
                            <img className="d-block w-100 rounded-circle"  src="https://dreamguys.co.in/demo/doccure/assets/img/features/feature-02.jpg" alt="" />
                        </div>
                        <h5>
                        Test Room
                            
              </h5>

                    </div>
                </div>

                <div className="swiper-slide">
                    <div>
                        <div className="item d-flex justify-content-center align-items-center mb-4">
                            <img className="d-block w-100 rounded-circle"  src="https://dreamguys.co.in/demo/doccure/assets/img/features/feature-04.jpg" alt="" />
                        </div>
                        <h5>
                        Laboratory 
                            
              </h5>

                    </div>
                </div>


                <div className="swiper-slide">
                    <div>
                        <div className="item d-flex justify-content-center align-items-center mb-4">
                            <img className="d-block w-100 rounded-circle"  src="https://dreamguys.co.in/demo/doccure/assets/img/features/feature-05.jpg" alt="" />
                        </div>
                        <h5>
                     Operation
                            
              </h5>

                    </div>
                </div>

                <div className="swiper-slide">
                    <div>
                        <div className="item d-flex justify-content-center align-items-center mb-4">
                            <img className="d-block w-100 rounded-circle"  src="https://dreamguys.co.in/demo/doccure/assets/img/features/feature-01.jpg" alt="" />
                        </div>
                        <h5>
                        Patient Ward
                            
              </h5>

                    </div>
                </div>

            </div>

            <div className="swiper-pagination swiper-pagination3"></div>

        </div>
    )
}
