import React, { useState, useEffect } from 'react';
import './header.scss';
export default function Header() {
    const [barToggle, setBarToggle] = useState(false);
    const [fixed, setFixed] = useState(false);
    useEffect(() => {
       window.onscroll = ()=> {
        if(document.documentElement.scrollTop > 200 || window.scrollTop > 200) {
            setFixed(true)
        } else {
            setFixed(false)
        }
       }
       
    });
    return (
        <header className={ fixed ? "header_fixed bg-light": " bg-light" }>
            <nav className="navbar position-relative navbar-expand-md justify-content-start navbar-dark text-black shadow_white_small py-0">

                <button onClick={() => setBarToggle(!barToggle)} className="navbar-toggler nav_btnToggle text-dark d-lg-none" type="button" data-toggle="collapse" data-target="#collapsibleNavId" aria-controls="collapsibleNavId"
                    aria-expanded="false" aria-label="Toggle navigation">
                    <i className="fas fa-bars    "></i>
                </button>

                <LogoImg />

                <div onClick={() => setBarToggle(!barToggle)}  className={barToggle ? "collapse navbar-collapse   sm_toggle_mobile d-block toggle_list" : "d-block toggle_list collapse navbar-collapse flex_basis_100"} id="collapsibleNavId">

                    <ul  className="navbar-nav mr-auto mt-0 mt-md-2 mt-lg-0 position-relative">
                    {barToggle ? (
                            <li className="logImg">
                                <LogoImg />
                                <BtnToggleRemove btnClick={() => setBarToggle(!barToggle)} />
                            </li>
                        ) : <li className="logImg d-md-none">
                                <LogoImg />
                                <BtnToggleRemove btnClick={() => setBarToggle(!barToggle)} />
                            </li>}

                     

                        <li className="nav-item active nav_home">
                            <a className="nav-link text-dark py-4 active_color" href="#">Home <span className="sr-only">(current)</span></a>

                        </li>
                        <li className="nav-item  nav_palette position-relative">
                            <a className="nav-link text-dark py-4" href="#">Doctors <i className="fas fa-chevron-down    "></i> </a>

                            <div className="hover_palette position-absolute w_200">
                                <div className="list-group ">
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Doctor Dashboard</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Appointments</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Schedule Timing</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Patients List</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Patients Profile</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Chat</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Invoices</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Profile Settings</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Reviews</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "><span>Doctor Regiser</span>  </a>

                                </div>
                            </div>
                        </li>
                        <li className="nav-item nav_palette">
                            <a className="nav-link text-dark py-4" href="#">Patients <i className="fas fa-chevron-down    "></i></a>


                            <div className="hover_palette position-absolute w_200">
                                <div className="list-group ">
                                    <button href="#" className=" third_palette_wrapper list-group-item d-flex justify-content-between list-group-item-action position-relative ">
                                        <span>Doctors</span>
                                        <i className="fas fa-chevron-right    "></i>
                                        <div className="third_palette position-absolute">
                                            <a href="#" className="px-2 py-3"> Map Grid </a>
                                            <a href="#" className="px-2 py-3"> Map List </a>
                                        </div>

                                    </button>
                                    <a href="#" className="list-group-item list-group-item-action "><span>Search Doctor</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Doctor Profile</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "><span>Booking</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Checkout</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Booking Success</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Patient Dashboard</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "><span>Favourites</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Chat</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "><span>Profile Settings</span>  </a>

                                </div>
                            </div>

                        </li>
                        <li className="nav-item nav_palette">
                            <a className="nav-link text-dark py-4" href="#">Pages <i className="fas fa-chevron-down    "></i></a>


                            <div className="hover_palette position-absolute w_200">
                                <div className="list-group ">
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Voice Call</span></a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Video Call</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Search Doctors</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Calender</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Components</span> </a>
                                    <button href="#" className=" third_palette_wrapper list-group-item d-flex justify-content-between list-group-item-action position-relative ">
                                        <span>Invoices </span>
                                        <i className="fas fa-chevron-right    "></i>
                                        <div className="third_palette position-absolute">
                                            <a href="#" className="px-2 py-3"> Invoices </a>
                                            <a href="#" className="px-2 py-3"> Invoices View </a>
                                        </div>

                                    </button>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Startter Page</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Login</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Register</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "><span>Forgot Password</span>  </a>

                                </div>
                            </div>
                        </li>
                        <li className="nav-item nav_palette">
                            <a className="nav-link text-dark py-4" href="#">Blogs <i className="fas fa-chevron-down    "></i></a>

                            <div className="hover_palette position-absolute w_200">
                                <div className="list-group ">
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Blog List</span>  </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Blog Grid</span> </a>
                                    <a href="#" className="list-group-item list-group-item-action "> <span>Blog Details</span> </a>

                                </div>
                            </div>
                        </li>

                        <li className="nav-item">
                            <a className="nav-link text-dark py-4" href="#">Admin</a>
                        </li>
                        {barToggle ? (
                            <li>
                                <LogSign display=" d-block" />
                            </li>
                        ) : null}

                    </ul>

                    <LogSign display="" />
                </div>
            </nav>
        </header>
    )
}
const LogoImg = () => (<a className="navbar-brand logo_brand text-dark py-0 mx-auto" href="#">

    <img className="w-100 " src="https://dreamguys.co.in/demo/doccure/assets/img/logo.png" alt="" />
</a>)


const BtnToggleRemove = ({ btnClick }) => (
    <button onClick={btnClick}>
        <i className="fas fa-times    "></i>
    </button>
)

const LogSign = ({ display }) => {
    return (

        <div className={"logSign d-none d-md-block" + display}>
            <a href="#" className=" px-3 py-2">
                <strong>Login/SignUp</strong>
            </a>
        </div>
    )
}